<template>
  <div>
    <div
      class="container-fluid wrap"
      v-if="apod"
    >
      <div class="row justify-content-between mt-1">
        <div class="col-6">
          {{ about }}
        </div>
        <div class="col-5">
          {{ apod.date }}
        </div>
      </div>
      <div>
        <div class="mt-2">
          <h1>{{ apod.title }}</h1>
        </div>
        <figure>
          <img
            v-bind:src="apod.url"
            class="figure-img img-fluid"
            v-bind:data-image-opened="apod.url"
            alt="x"
          >
        </figure>
      </div>
      <button
        onclick="responsiveVoice.speak($('#text').val(),'US English Male', {pitch:1.4, rate:1.25});"
        type="button"
        value="Play"
        class="btn btn-dark btn-sm btn-block"
      >
        Play
      </button>

      <blockquote class="mt-1">
        <textarea-autosize
          readonly
          id="text"
          cols="50"
          v-model="apod.explanation"
          class="mt-2 text-selection"
        ></textarea-autosize>
        <footer class="blockquote-footer">
          <cite>{{ apod.copyright }}</cite></footer>
      </blockquote>

    </div>
    <div v-else>
      loading...<br> <em>or better check your internet.</em>
    </div>
    <hr>
    <p class="text-center">
      <em>made with vue & nasa</em><br>
      <em>2019</em>
    </p>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'APOD',
  data () {
    return {
      about: 'Astronomy Picture of the Day',
      apod: ''
    }
  },
  mounted () {
    axios.get('https://api.nasa.gov/planetary/apod?api_key=vf64R8zpJBXhv3IIw4argAXnALVdrDRLyNzts8lb').then(response => (this.apod = response.data)).catch(error => console.log(error))
  }
}
</script>

<style>
h1 {
  font-size: 1.75rem !important;
}
.wrap {
  max-width: 85vw;
  margin: 2vh auto;
}
img,
iframe {
  max-width: 100%;
  max-height: 75vh;
}
textarea,
textarea-autosize {
  resize: none !important;
  border-style: none;
  resize: both;
  overflow: auto;
  background-color: #1d1d1d;
  color: #6c757d;
  outline: none;
  max-width: 100%;
  hyphens: auto;
  text-align: justify;
}
.text-selection::selection {
  background: #008fff;
  color: white;
}
</style>
